.basic-btn{
  border: 3px solid rgba(0,0,0,0.3);
  border-radius: 5px;
  padding: 2px 10px;
  margin-top: 20px;
  cursor: pointer;
}


.react-js-diagrams-canvas {
  position: relative;
  flex-grow: 1;
  display: flex;
  cursor: move;
  overflow: hidden;

  background-color: rgb(60, 60, 60);
  background-size: 50px 50px;
  display: flex;
  background-image: linear-gradient( 0deg , transparent 24%, rgba(255, 255, 255, 0.05) 25%, rgba(255, 255, 255, 0.05) 26%, transparent 27%, transparent 74%, rgba(255, 255, 255, 0.05) 75%, rgba(255, 255, 255, 0.05) 76%, transparent 77%, transparent), linear-gradient( 90deg , transparent 24%, rgba(255, 255, 255, 0.05) 25%, rgba(255, 255, 255, 0.05) 26%, transparent 27%, transparent 74%, rgba(255, 255, 255, 0.05) 75%, rgba(255, 255, 255, 0.05) 76%, transparent 77%, transparent);
   

  .point {
    fill: rgba(white,0.5);
    &.selected {
      fill: rgb(0,192,255);
    }
  }

  .selector {
    position: absolute;
    background-color: rgba(0,192,255,0.2);
    border: solid 2px rgb(0,192,255);
  }

  svg {
    position: absolute;
    height: 100%;
    width: 100%;
    transform-origin: 0 0;
    overflow: visible;
  }

  .node-view {
    top:0;
    left:0;
    right:0;
    bottom:0;
    position: absolute;
    pointer-events: none;
    transform-origin: 0 0;
    z-index: 1;
  }

  .node {
    position: absolute;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none;   /* Chrome/Safari/Opera */
    user-select: none;
    cursor: move;
    pointer-events: all;

    &.selected {
      >* {
        border-color:rgb(0,192,255) !important;
        -webkit-filter: drop-shadow( 0 0 20px rgba(0,192,255,0.5));
      }
    }
  }

  @keyframes dash {
    from {
      stroke-dashoffset: 24;
    }
    to {
      stroke-dashoffset: 0;
    }
  }

  path{
    fill:none;
    pointer-events:all;

    &.selected {
      stroke: rgb(0,192,255) !important;
      stroke-dasharray: 10,2;
      animation: dash 1s linear infinite;
    }
  }

  .basic-node {
    background-color: rgb(30,30,30);
    border-radius: 5px;
    font-family:Arial;
    color: white;
    border: solid 2px black;
    overflow: visible;
    font-size: 11px;
    box-shadow: 0 0 10px rgba(black,0.5);

    .title {
      display: flex;
      white-space: nowrap;
      // >* {
      //   align-self: center;
      // }
      .fa {
        padding: 5px;
        opacity: 0.2;
        cursor: pointer;

        &:hover {
          opacity: 1.0;
        }
      }
      .name {
        flex-grow: 1;
        padding: 5px 5px;
      }
    }

    .ports {
      display: flex;
      background-image: linear-gradient(rgba(black,0.1),rgba(black,0.2));
      .in, .out {
        flex-grow: 1;
        display: flex;
        
      }
      .in-port, .out-port {
        display: flex;
        .name {
          padding: 0 5px;
        }
      }
      .in-port{
        .port {
          text-align: center;
          width: 29px;
          height: 29px;
          background: rgb(11, 159, 218);
          align-self: center;
          border: 2px ridge rgb(18, 21, 195);
          &:hover,&.selected {
            background: rgb(3, 2, 68);
          }
        }
        // background-color: rosybrown;
      }
      .out-port {
        // background-color: darkseagreen;
        justify-content: flex-end;
        .name {
          justify-content: flex-end;
          text-align: right;
        }
        .port {
          text-align: center;
          margin-left: 3px;
          width: 29px;
          height: 29px;
          background: #cf9c10;
          align-self: center;
          border: 2px ridge rgb(195, 18, 18);
          &:hover,&.selected {
            background: rgb(61, 6, 6);
          }
        }
      }
    }
  }

}
